___CSS_LOADER_EXPORT___.push([module.id, `.alogocarga {
    position: fixed !important;
    display: flex;
    flex-direction: column;
    z-index: 1000 !important;
    top: 0 !important;
    left: 0 !important;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.alogocarga-contenedor {
    position: fixed;
    width: 20%;
    height: 20%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.alogocarga-imagen {
    width: 30%;
    align-items: center;
    justify-content: center;
    margin: auto;
}

.alogocarga-imagen img {
    display: block;
    width: 100%;
    margin-bottom: 10px;
    animation-name: girarLogoCarga;
    animation-duration: 4s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

.alogocarga-texto {
    font-size: 18px;
    font-weight: bold;
    color: #fff;
    text-align: center;
    margin: auto;
}

@keyframes girarLogoCarga {
    0% {
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.alogocarga-lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

.alogocarga-lds-ellipsis div {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #fff;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.alogocarga-lds-ellipsis div:nth-child(1) {
    left: 8px;
    animation: lds-ellipsis1 0.6s infinite;
}

.alogocarga-lds-ellipsis div:nth-child(2) {
    left: 8px;
    animation: lds-ellipsis2 0.6s infinite;
}

.alogocarga-lds-ellipsis div:nth-child(3) {
    left: 32px;
    animation: lds-ellipsis2 0.6s infinite;
}

.alogocarga-lds-ellipsis div:nth-child(4) {
    left: 56px;
    animation: lds-ellipsis3 0.6s infinite;
}

@keyframes lds-ellipsis1 {
    0% {
        transform: scale(0);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes lds-ellipsis3 {
    0% {
        transform: scale(1);
    }

    100% {
        transform: scale(0);
    }
}

@keyframes lds-ellipsis2 {
    0% {
        transform: translate(0, 0);
    }

    100% {
        transform: translate(24px, 0);
    }
}`, "",{"version":3,"sources":["webpack://./src/acomponentes/ALogoCarga/ALogoCarga.css"],"names":[],"mappings":"AAAA;IACI,0BAA0B;IAC1B,aAAa;IACb,sBAAsB;IACtB,wBAAwB;IACxB,iBAAiB;IACjB,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,oCAAoC;AACxC;;AAEA;IACI,eAAe;IACf,UAAU;IACV,WAAW;IACX,MAAM;IACN,OAAO;IACP,QAAQ;IACR,SAAS;IACT,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,UAAU;IACV,mBAAmB;IACnB,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI,cAAc;IACd,WAAW;IACX,mBAAmB;IACnB,8BAA8B;IAC9B,sBAAsB;IACtB,iCAAiC;IACjC,mCAAmC;AACvC;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,WAAW;IACX,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI;QACI,+BAA+B;QAC/B,4BAA4B;QAC5B,2BAA2B;QAC3B,0BAA0B;QAC1B,uBAAuB;IAC3B;;IAEA;QACI,iCAAiC;QACjC,8BAA8B;QAC9B,6BAA6B;QAC7B,4BAA4B;QAC5B,yBAAyB;IAC7B;AACJ;;AAEA;IACI,qBAAqB;IACrB,kBAAkB;IAClB,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,gBAAgB;IAChB,mDAAmD;AACvD;;AAEA;IACI,SAAS;IACT,sCAAsC;AAC1C;;AAEA;IACI,SAAS;IACT,sCAAsC;AAC1C;;AAEA;IACI,UAAU;IACV,sCAAsC;AAC1C;;AAEA;IACI,UAAU;IACV,sCAAsC;AAC1C;;AAEA;IACI;QACI,mBAAmB;IACvB;;IAEA;QACI,mBAAmB;IACvB;AACJ;;AAEA;IACI;QACI,mBAAmB;IACvB;;IAEA;QACI,mBAAmB;IACvB;AACJ;;AAEA;IACI;QACI,0BAA0B;IAC9B;;IAEA;QACI,6BAA6B;IACjC;AACJ","sourcesContent":[".alogocarga {\r\n    position: fixed !important;\r\n    display: flex;\r\n    flex-direction: column;\r\n    z-index: 1000 !important;\r\n    top: 0 !important;\r\n    left: 0 !important;\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: rgba(0, 0, 0, 0.5);\r\n}\r\n\r\n.alogocarga-contenedor {\r\n    position: fixed;\r\n    width: 20%;\r\n    height: 20%;\r\n    top: 0;\r\n    left: 0;\r\n    right: 0;\r\n    bottom: 0;\r\n    margin: auto;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n}\r\n\r\n.alogocarga-imagen {\r\n    width: 30%;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin: auto;\r\n}\r\n\r\n.alogocarga-imagen img {\r\n    display: block